* {
  margin: 0;
  padding: 0;
  font-family: Noto Sans, Fira Sans, Open Sans, sans-serif;
  font-size: 12px;
}

table {
  border-collapse: collapse;
  width: 100%;
}

table td {
  height: 20px;
  padding: 3px;
}

table td.hr {
  background-color: #fff;
  height: auto;
}

table.meeting {
  border-spacing: 9px;
}

table.meeting > tbody.apply > tr:not(:first-child):nth-child(odd), table.meeting > tbody.living > tr:not(:first-child):nth-child(odd), table.meeting > tbody.treasures > tr:not(:first-child):nth-child(odd) {
  background-color: #f1f1f1;
}

table.schedule > tbody {
  counter-reset: page;
}

table.schedule > tbody > tr span.page {
  text-align: right;
  padding: 0 8px;
  display: none;
}

table.schedule > tbody > tr:nth-child(2n) {
  page-break-after: always;
}

table.schedule > tbody > tr:nth-child(2n) span.page {
  display: block;
}

table.schedule > tbody > tr:nth-child(2n) span.page:after {
  counter-increment: page;
  content: " " counter(page);
}

table thead tr td:nth-child(2) {
  text-align: right;
  font-family: Oranienbaum;
  font-size: 25px;
}

table thead tr td:first-child {
  text-transform: uppercase;
  vertical-align: bottom;
  font-size: 15px;
}

tbody.apply tr:first-child td, tbody.living tr:first-child td, tbody.treasures tr:first-child td {
  font-weight: bold;
}

tbody.apply tr:first-child td:first-child, tbody.living tr:first-child td:first-child, tbody.treasures tr:first-child td:first-child {
  justify-content: center;
  padding: 5px;
  display: flex;
}

tbody.apply tr:first-child td img, tbody.living tr:first-child td img, tbody.treasures tr:first-child td img {
  float: left;
  max-height: 20px;
}

tbody.treasures tr:first-child td:first-child {
  background-color: #2a6b77;
}

tbody.treasures tr td {
  color: #2a6b77;
}

tbody.apply tr:first-child td:first-child {
  background-color: #9b6d17;
}

tbody.apply tr td {
  color: #9b6d17;
}

tbody.living tr:first-child td:first-child {
  background-color: #bf2f13;
}

tbody.living tr td {
  color: #bf2f13;
}

tr.apply td, tr.living td, tr.treasures td {
  white-space: normal;
  font-size: 16px;
  font-weight: 600;
  line-height: 19px;
}

tr.head td.label {
  font-size: 14px;
}

td.time {
  width: 40px;
}

td.time, td.title {
  text-align: center;
  font-weight: bold;
}

td.title, td.assigned {
  white-space: nowrap;
  width: .1%;
}

td.title {
  text-align: right;
}

td.label {
  text-transform: uppercase;
  font-weight: bold;
}

div.message {
  text-align: center;
  background-color: #f1f1f1;
  margin: 8px;
  padding: 29px 0;
  font-size: 22px;
}

div.message p {
  font-size: 50px;
}

div.message, div.message p {
  font-family: Oranienbaum;
}

